import React, { useState, useRef, useEffect } from 'react'
import './index.scss'
import Display from './Display'
import Keypad from './Keypad'
import Button from './Button'
import { useDimension, useOutside } from 'Page/StockEntry/GeneralServices'
// import {} from './services'

const Calculator = (props) => {
    const [data, setData] = useState('')
    const wrapperRef = useRef(null);

    const calculate = (onChange) => {
        try {
            const result = eval(data);
            const value = {
                value: result?.toString(),
                name: props.name
            }

            onChange(value)
            // setData(result);
        } catch (e) {
            console.log(e);
            setData('')
        }
    }

    useOutside({ wrapperRef, setShow: props.setShowCalculator, show: props.showCalculator });

    const { isMobile } = useDimension()
    // track element position
    let elem = null
    if (!props.singleView) elem = document?.getElementById(`dropdown${props.entryListIdx}${props.poListIdx}`);
    else elem = null
    let position = elem?.getBoundingClientRect()
    let menuOnTop = position?.bottom > 600 ? true : false
    if (isMobile) menuOnTop = position?.bottom > 400 ? true : false

    useEffect(() => {
        position = elem?.getBoundingClientRect()
    }, [props.showCalculator])

    const top = position?.top - (menuOnTop ? (isMobile ? 400 : 320) : (isMobile ? 50 : -30))
    const handleClick = e => {
        const value = e.target.getAttribute('data-value');
        switch (value) {
            case 'clear':
                props.onChange({
                    value: '',
                    name: props.name
                })
                break;
            case 'clearall':
                props.onChange({
                    value: '',
                    name: props.name
                })
                setData('');
                break;
            case 'equal':
                calculate(props.onChange);
                props.setShowCalculator()
                break;
            default:
                setData(data + value);
        }
    }
    return (
        <div className={`${props.useCalculator ? null : 'd-none'} calculator ${props.className} ${isMobile ? 'px-5' : false}`} ref={wrapperRef} style={{ top, left: isMobile ? 0 : false, width: isMobile ? '100%' : false, right: props.singleView ? '1rem' : false }}>
            <Display data={data} setData={setData} />
            <Keypad>
                <table >
                    <tr>
                        <td style={{ backgroundColor: 'rgba(168, 177, 190, 1)', color: 'white' }}><Button onClick={handleClick} label="x" value="*" /></td>
                        <td style={{ backgroundColor: 'rgba(168, 177, 190, 1)', color: 'white' }}><Button onClick={handleClick} label="/" value="/" /></td>
                        <td style={{ backgroundColor: 'rgba(168, 177, 190, 1)', color: 'white' }}><Button onClick={handleClick} label="+" value="+" /></td>
                        <td style={{ backgroundColor: 'rgba(168, 177, 190, 1)', color: 'white' }}><Button onClick={handleClick} label="-" value="-" /></td>
                    </tr>
                    <tr>
                        <td><Button onClick={handleClick} label="7" value="7" /></td>
                        <td><Button onClick={handleClick} label="8" value="8" /></td>
                        <td><Button onClick={handleClick} label="9" value="9" /></td>
                        <td style={{ backgroundColor: 'rgba(74, 190, 132, 1)', color: 'white' }} ><Button onClick={handleClick} label="CE" value="clear" /></td>
                    </tr>
                    <tr>
                        <td><Button onClick={handleClick} label="4" value="4" /></td>
                        <td><Button onClick={handleClick} label="5" value="5" /></td>
                        <td><Button onClick={handleClick} label="6" value="6" /></td>
                        <td style={{ backgroundColor: 'rgba(74, 190, 132, 1)', color: 'white' }} ><Button onClick={handleClick} label="C" value="clearall" /></td>
                    </tr>
                    <tr>
                        <td><Button onClick={handleClick} label="1" value="1" /></td>
                        <td><Button onClick={handleClick} label="2" value="2" /></td>
                        <td><Button onClick={handleClick} label="3" value="3" /></td>
                        <td style={{ backgroundColor: 'rgba(74, 190, 132, 1)', color: 'white' }} rowSpan={2}><Button onClick={handleClick} label="=" value="equal" /></td>
                    </tr>
                    <tr>
                        <td colSpan={2}><Button onClick={handleClick} label="0" value="0" twoSpace={true} /></td>
                        <td><Button onClick={handleClick} label="." value="." /></td>
                    </tr>
                </table>
            </Keypad>
        </div >
    )
}
export default Calculator