import React from 'react'
import { useSelector } from 'react-redux'
import Dropdown from 'Component/Dropdown/index'
import Decimal from 'Component/Input/Decimal'
import Inputs from 'Component/Input'
import Input from 'Component/Input/Pack'
import Note from 'Component/Input/Note'
import Excess from 'Component/Input/Excess'


const WeightEntry = ({
    entryListIdx,
    wmsPoSkuInfoId,
    gradeList,
    ripeness,
    inputWidth,
    showTitle,
    item,
    onChange,
    onChangeDropdown,
    uom,
    poListIdx,
    category,
    showSlot,
    targetRepacks,
    onEnter
}) => {
    const ripenessLevel = useSelector(state => state.ripenessLevel)
    const slotList = useSelector(state => state.slotList)
    const wasteSemiWaste = item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? true : false

    const targetRepackList = targetRepacks?.map(t => Number.parseInt(t.slot))
    const filteredSlotList = slotList?.filter(s => targetRepackList?.includes(Number.parseInt(s.value)))

    const unitOfMeasuress = item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? uom : (item?.grade?.uom?.toLowerCase() === 'gram' ? 'kg' : item?.grade?.uom)
    return (
        <div className='d-flex w-100 we-m' onKeyDown={e => e.keyCode === 13 ? onEnter() : false}>
            {/* <Dropdown
                show={showSlot}
                isEmpty={item.slotIsEmpty}
                poListIdx={poListIdx}
                selectedValue={item?.slot}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'slot' })}
                title='Slot'
                placeholder='Select Slot'
                options={filteredSlotList}
                width='w-30'
                showTitle={showTitle}
                className={item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? 'd-none' : null} /> */}
            <Dropdown
                show={true}
                isEmpty={item.gradeIsEmpty}
                ddType={'grade'}
                poListIdx={poListIdx}
                selectedValue={item?.grade}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'grade' })}
                title='Grade'
                placeholder='Select Grade'
                options={gradeList}
                width='w-30'
                showTitle={showTitle}
                className={null} />
            <Input
                useCalculator={true}
                packThreshold={item.packThreshold}
                className={wasteSemiWaste ? 'd-none' : null}
                isEmpty={item.packIsEmpty || item.packThresholdIsEmpty}
                entryListIdx={entryListIdx}
                poListIdx={poListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={'PACK'}
                inputWidth={inputWidth}
                values={item?.pack}
                showTitle={showTitle}
                title={'Input Pack'}
                onChange={(e, val) => onChange(e, val)} />
            <Inputs
                useCalculator={true}
                isEmpty={item.qtyIsEmpty}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={unitOfMeasuress}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qty}
                showTitle={showTitle}
                title={'Input Quantity'}
                onChange={(e, val) => onChange(e, val)} />
            <Decimal
                useCalculator={true}
                isEmpty={item.qtyIsEmpty}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={unitOfMeasuress}
                inputWidth={inputWidth}
                values={item?.qty}
                title={'Input Weight'}
                showTitle={showTitle}
                onChange={(e, val) => onChange(e, val)} />

            {/* 30-12-2020 */}
            {/* <Decimal
                toCompare={true}
                isEmpty={item.qtyToCompareIsEmpty}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom !== 'kg' ? null : unitOfMeasuress === 'kg' ? null : 'KG'}
                ripeness={ripeness}
                inputWidth={inputWidth}
                values={item?.qtyToCompare}
                title={'Input Weight'}
                showTitle={showTitle}
                onChange={(e) => onChange(e)} /> */}
            {/* <Dropdown
                // isDisabled={entryListIdx === 0 ? true : false}
                isEmpty={item.ripenessIsEmpty}
                selectedValue={item?.ripeness}
                grade={item.grade?.label}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                uom={uom}
                show={true}
                onChangeDropdown={(selected) => onChangeDropdown({ selected, name: 'ripeness' })}
                title='Ripeness'
                placeholder='Select Ripeness'
                options={ripenessLevel}
                width='w-30'
                showTitle={showTitle}
                className={item?.grade?.label === 'Waste' || item?.grade?.label === 'Semi Waste' ? 'd-none' : (category === 'Fruits' ? null : 'd-none')} /> */}
            <Note
                isEmpty={item.noteIsEmpty}
                grade={item.grade?.label}
                poListIdx={poListIdx}
                entryListIdx={entryListIdx}
                wmsPoSkuInfoId={wmsPoSkuInfoId}
                ripeness={ripeness}
                inputWidth={'w-50'}
                values={item?.note}
                showTitle={showTitle}
                title={'Note'}
                onChange={(e) => onChange(e)} />

            <Excess
                hidePercent={true}
                className={wasteSemiWaste ? 'd-none' : null}
                packThreshold={item?.qty / item?.pack}
                isEmpty={false}
                inputWidth={inputWidth}
                showTitle={showTitle}
                title={'Average Pack'} />

            <Excess
                className={wasteSemiWaste ? 'd-none' : null}
                packThreshold={item.packThreshold}
                isEmpty={item.packThresholdIsEmpty}
                inputWidth={inputWidth}
                showTitle={showTitle}
                title={'Excess'} />

        </div>
    )
}
export default WeightEntry