import React, { useState } from "react";
import { isLessThanInitWeight } from 'services'
import {
    CButton,
    CCol,
    CModal,
    CModalBody,
    CRow,
    CModalFooter
} from "@coreui/react";
import "Component/Modal/PopUp/index.scss";
import { threshold, initialWeight, defaults } from './services'
import Dropdown from 'Component/Dropdown'

const selectEmptyExceed = ({ setIsRequired, traceEmptyRow, data, invalidThreshold, setInvalidThreshold }) => {
    if (invalidThreshold) setInvalidThreshold(false)
    setIsRequired(false)
    if (typeof (traceEmptyRow) === 'function') traceEmptyRow(data)
}

const PopUpRequired = ({ isRequired, setIsRequired, moduleName, module, totalSku, invalidThreshold, setInvalidThreshold, submit, totalEmptyThreshold, traceEmptyRow }) => {
    let saved = defaults;
    let isTrue

    const dimention = document.documentElement.clientHeight
    const height = dimention - 400

    if ((!isRequired && invalidThreshold) || (isRequired && invalidThreshold)) {
        saved = threshold
    }

    if (moduleName !== 'stockTake') {
        isTrue = isLessThanInitWeight(module)
        if (isTrue) saved = initialWeight
    }

    const focus = document.getElementById('btn_is_required')
    if (focus) focus.focus()

    //repacking state for filter
    const [filter, setFilter] = useState({ value: 0, label: 'All Exceed' })
    return (
        <CRow className={isRequired || invalidThreshold ? null : 'd-none'}>
            <CCol className='required'>
                <CModal
                    show={isRequired || invalidThreshold}
                    onClose={() => {
                        setIsRequired(false)
                        if (invalidThreshold) setInvalidThreshold(false)
                    }}
                    size="lg"
                    style={{ minHeight: "280px" }}
                    bodyOpenClassName="p-3"
                    closeOnBackdrop={false}
                >
                    <div className="d-flex flex-row-reverse">
                        <i className="pr-3 pt-3 iconU-modalClose" onClick={() => {
                            setIsRequired(false)
                            if (invalidThreshold) setInvalidThreshold(false)
                        }} ></i>
                    </div>
                    <CModalBody className="px-5 mt-3 py-0">
                        <div className='d-flex align-items-center mb-5'>
                            <label className={`${saved?.textColor} LatoBold mr-2 h5`}>{saved?.header}</label> <span><i className={`${saved?.headerIcon}`}></i></span>
                        </div>
                        <div style={{ backgroundColor: '#F5F7FA' }} className='w-100 border rounded px-3 pt-4 pb-3 mb-4'>
                            <div className={`h5 mb-3 ${isRequired || invalidThreshold ? null : 'd-none'}`}>{saved?.date}</div>
                            <div className='mb-0'>
                                <label className='mr-1 h5'>{`${saved?.status}`}</label>
                            </div>
                        </div>
                        <div className='border-all w-100' style={{ maxHeight: height, minHeight: height, overflow: 'scroll', display: totalEmptyThreshold?.length && moduleName === 'repack' ? null : 'none' }}>
                            <table>
                                <tr className='emptyTextField' style={{ borderBottom: '1px solid #e4e4e4' }}>
                                    <th className='w-50'>SKU List</th>
                                    <td className='w-50' style={{ textAlign: 'right' }}>
                                        <Dropdown
                                            noMarginBottom={true}
                                            zInfinity={true}
                                            show={true}
                                            showTitle={false}
                                            options={[{ value: 1, label: 'Less than -20%' }, { value: 2, label: 'More than 50%' }]}
                                            onChangeDropdown={(selected) => setFilter(selected)}
                                            title='All Exceed'
                                            placeholder='Exceed'
                                            moduleName={moduleName}
                                            width='w-100'
                                            selectedValue={filter}
                                        /></td>
                                </tr>

                                <tbody>
                                    {
                                        totalEmptyThreshold?.filter(item => {
                                            const { value } = filter
                                            if (value === 1) return item.excess < -20
                                            if (value === 2) return item.excess > 50
                                            else return item
                                        })?.map(data => (
                                            <tr onClick={() => selectEmptyExceed({ setIsRequired, traceEmptyRow, data, invalidThreshold, setInvalidThreshold })} style={{ borderBottom: '1px solid #e4e4e4' }} className='cursor-pointer excess-row'>
                                                <td>{data.skuName}</td>
                                                <td style={{ textAlign: 'right' }} className='red'>{`${data.excess} %`}<span className='iconU-arrowRightAfter text' /></td>
                                            </tr>
                                        ))
                                    }
                                </tbody>
                            </table>
                        </div>
                    </CModalBody>
                    <CModalFooter className={`px-5 pb-5 pt-0 w-100 ${invalidThreshold ? 'justify-content-between' : 'justify-content-start'}  border-none d-flex mt-2`}>
                        <div>
                            {/* <CButton onClick={() => {
                                setIsRequired(false)
                                if (invalidThreshold) setInvalidThreshold(false)
                            }} className={`text no-shadow ${`bg-softGreen ${isRequired ? 'd-none' : null}`}`}>
                                Cancel
                        </CButton> */}
                            <CButton
                                id={'btn_is_required'}
                                tabIndex='0'
                                onClick={() => {
                                    setIsRequired(false)
                                    if (invalidThreshold) setInvalidThreshold(false)
                                }} className={`text-white no-shadow ${saved?.button}`}>
                                {saved?.buttonText}
                            </CButton>
                        </div>
                        <div>
                            <CButton
                                style={{ backgroundColor: '#F5F7FA' }}
                                id={'btn_is_required'}
                                tabIndex='0'
                                onClick={() => {
                                    setIsRequired(false)
                                    if (invalidThreshold) setInvalidThreshold(false)
                                    if (typeof (submit) === 'function') submit()
                                }} className={`text LatoBold no-shadow ${invalidThreshold ? null : 'd-none'} `}>
                                Submit anyway
                        </CButton>
                        </div>

                    </CModalFooter>
                </CModal>
            </CCol>
        </CRow>

    );
};

export default PopUpRequired;
