import React, { useEffect } from "react";
import {
  CNav,
  CNavItem,
  CNavLink,
  CTabs,
} from '@coreui/react'
import Trash from "assets/icons/trash.png";
import WeightEntry from "Page/StockEntry/Repacking/WeightEntry/WeightEntryList/WeightEntryCard/WeightEntry";
import "./index.scss";
import {
  addRow,
  updateJSWeightLine,
  updateJSWeightLineDropdown
} from "Page/StockEntry/Repacking/WeightEntry/services";
import { deleteRow, countQty } from 'Page/StockEntry/GeneralServices/moduleServices'
import { useDispatch } from "react-redux";
import { isFloat } from 'services';

const WeightEntryCard = ({
  inputWidth,
  activeSku,
  poListIdx,
  poItem,
  moduleEntry,
  moduleName,
  gradeList,
  activeTab,
  setActiveTab
}) => {
  const dispatch = useDispatch();
  const {
    initialWeight,
    inboundQty,
    initialWeightUpdate,
    initialWeightScreenUpdate,
    uom,
    edited,
    category,
    codeGrade,
    isHidden,
    targetRepacks
  } = poItem;
  const initialWeightTotal = !isNaN(initialWeightScreenUpdate) ? initialWeightScreenUpdate : initialWeight
  const uomType = uom === "kg" || uom === "gram" ? "Weight" : "Quantity";
  const skuName = moduleEntry.jobPost.list[activeSku].skuName

  useEffect(() => {
    if (targetRepacks?.length) {
      setActiveTab(targetRepacks[0]?.slot)
    }
  }, [activeSku])
  return (
    <div className='target-repack'>
      <CTabs>
        <CNav variant="tabs">
          <CNavItem className='d-flex'>
            {targetRepacks?.map(data => {
              if (data.skuName === skuName) return <CNavLink isActive={Number(data?.slot) === Number(activeTab)} active={Number(data?.slot) === Number(activeTab)} onClick={() => setActiveTab(data?.slot)}>{`Slot ${data?.slot}`}</CNavLink>
            })}
          </CNavItem>
        </CNav>
      </CTabs>
      <div
        // onKeyDown={e => e.keyCode === 13 ? addRow({dispatch, moduleEntry,moduleName, activeSku, poListIdx}) : false}
        id={`sku_item_${activeSku}_${poListIdx}`}
        // tabIndex='0'
        className={`${isHidden ? 'd-none' : null} d-flex flex-column sku-item justify-content-between w-100 mb-3 align-items-center border rounded-bottom border-soft-green ${edited ? "soft-green" : null
          }`}
      >
        {/* new logic */}
        <div className='w-100'>
          <div className="w-100 border-soft-green mt-3 d-flex align-items-start px-3 border-bottom border-soft-green mb-3">
            <div className="w-100 d-flex justify-content-between align-items-center LatoBold cardheader-m">
              <div className="d-flex align-items-center cardheader-m">
                <div className="d-flex align-items-center mb-3 mr-5">
                  {`Combined PO SKU`}
                </div>
              </div>
              <div className="d-flex align-items-center mb-3">
                {`Initial ${uomType}`}
                <span className="ml-2 font-green">
                  {` ${initialWeight} ${uom?.toUpperCase()}${initialWeight > 1 ? "s" : ""}`}
                </span>
              </div>
            </div>
          </div>

          <div className="d-flex flex-column justify-content-between w-100 align-items-center px-3">
            {poItem.itemList?.map((item, entryListIdx) => {
              return (
                <div className={`d-flex w-100 align-items-center justify-content-between ${targetRepacks?.length ? (item?.slot?.value != activeTab ? 'd-none' : null) : null}`}>
                  <div className="w-100 d-flex">
                    <div style={{ width: '10px' }} className={`input-height d-flex align-items-center mb-3 mr-3 pt-4 mt-2 ${targetRepacks?.length ? 'd-none' : null}`}>{`${entryListIdx + 1}.`}</div>
                    <div className="w-100">
                      <WeightEntry
                        poListIdx={poListIdx}
                        onEnter={() => {
                          addRow({
                            dispatch, moduleEntry,
                            moduleName, activeSku, poListIdx, activeTab
                          })
                          setTimeout(() => {
                            const focus = document.getElementById(`pack_${poListIdx}_${entryListIdx + 1}`)
                            if (focus) focus.focus()
                          }, 10);

                        }}
                        showSlot={targetRepacks?.length ? true : false}
                        targetRepacks={targetRepacks}
                        category={category}
                        entryListIdx={entryListIdx}
                        wmsPoSkuInfoId={item.wmsPoSkuInfoId}
                        showTitle={true}
                        gradeList={gradeList}
                        onChange={(e, val) =>
                          updateJSWeightLine({
                            dispatch,
                            moduleEntry,
                            moduleName,
                            activeSku,
                            poListIdx,
                            entryListIdx,
                            e,
                            val
                          })
                        }
                        onChangeDropdown={({ selected, name }) =>
                          updateJSWeightLineDropdown({
                            dispatch,
                            moduleEntry,
                            moduleName,
                            activeSku,
                            poListIdx,
                            entryListIdx,
                            selected,
                            name,
                          })
                        }
                        uom={uom}
                        inputWidth={inputWidth}
                        item={item}
                      />
                    </div>
                  </div>
                  <div className="d-flex w-10 align-items-center justify-content-end input-height mb-0 p-0">
                    <img
                      src={Trash}
                      alt="delete icon"
                      className="w-1 cursor-pointer"
                      onClick={(e) =>
                        deleteRow({
                          dispatch,
                          moduleEntry,
                          moduleName,
                          activeSku,
                          poListIdx,
                          entryListIdx,
                          e,
                          countQty
                        })
                      }
                    />
                  </div>
                </div>
              );
            })}
          </div>
          <div
            onKeyDown={e => e.keyCode === 13 ? addRow({ dispatch, moduleEntry, moduleName, activeSku, poListIdx }) : false}
            // className={`w-100 ml-5 pl-4 font-green mb-3 LatoBold cursor-pointer ${total === 0 ? 'd-none' : null}`}
            className={`w-100 ${targetRepacks?.length ? 'pl-3' : 'ml-5'} font-green mb-3 LatoBold cursor-pointer `}
            // tabIndex='0'
            onClick={() => addRow({
              dispatch, moduleEntry,
              moduleName, activeSku, poListIdx, activeTab
            })}
          >
            <i className="green iconU-entryAdd pr-2"></i>
            {`Add ${uomType} (${isFloat(initialWeightTotal) ? isFloat(initialWeightTotal) : initialWeightTotal} ${uom ? uom.toUpperCase() : ''}${initialWeightTotal > 1 ? "s" : ""} left)`}
          </div>
        </div>
      </div>
    </div>

  );
};
export default WeightEntryCard;
