import React from 'react'
import { isFloat } from 'services'
const Excess = ({
    isEmpty,
    entryListIdx,
    wmsPoSkuInfoId,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    placeholder,
    grade,
    title,
    className,
    packThreshold,
    hidePercent
}) => {
    const show = uom === 'gram' || uom === 'kg' ? false : true
    return (
        <div className={`${inputWidth} mb-3 mr-3 ${className} ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold text-nowrap ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={` ${isEmpty ? 'tooltip-custom' : 'tooltip-custom1'} w-100 d-flex align-items-center input-height px-2 mr-3`}>
                <div className={`LatoBold ${inputWidth} ${isEmpty ? 'red' : 'green'}`}>{`${packThreshold && !isNaN(parseInt(packThreshold)) ? `${isFloat(packThreshold)}${hidePercent ? '' : '%'}` : (hidePercent ? '0' : '0%')}`}</div>
                <span className={`${isEmpty ? 'tooltiptext' : 'd-none'}`}>
                    <span style={{ fontWeight: 600 }}>Threshold Error</span>
                    <br />
                    <span>{`Cannot be larger than 50%. Your current threshold is ${packThreshold}%`}</span>
                </span>
            </div>
        </div>
    )
}

export default Excess