import React from 'react'

const Button = (props) => {
    return (
        <div
            className="button w-100"
            onClick={props.onClick}
            data-size={props.size}
            data-value={props.value}
            data-twoSpace={props.twoSpace}
        >
            {props.label}
        </div>
    )
}

export default Button