import React, { useState, useEffect } from 'react'
import { Redirect } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import SelectedSKUList from 'Page/StockEntry/Repacking/WeightEntry/SelectedSKUList'
import WeightEntryList from 'Page/StockEntry/Repacking/WeightEntry/WeightEntryList'
import { getPoRepack } from 'Page/StockEntry/GeneralServices/moduleServices'

const WeightEntry = () => {
    const repackEntryData = useSelector(state => state.repackEntryData)
    const dispatch = useDispatch()
    const [hideSKUList, setHideSKUList] = useState(false)
    const [activeSku, setActiveSKU] = useState(0)
    const [updateEntry, setUpdateEntry] = useState()
    let height = document.documentElement.clientHeight
    let clientWidth = document.documentElement.clientWidth
    let heightAdj = height - 150
    const moduleName = 'repack'

    useEffect(_ => {
        if (repackEntryData?.jobPost.list.length > 0) getPoRepack({ dispatch, moduleName, moduleEntry: repackEntryData, activeSku })
    }, [activeSku])


    if (!repackEntryData || repackEntryData?.jobPost.list.length === 0) {
        return (
            <div className='d-flex align-items-center justify-content-center text-center w-100 h-screen'><Redirect to='/inventory-entry/repacking'>Please Select At Least 1 SKU</Redirect></div >)
    }
    return (
        <div style={{ height: clientWidth <= 500 ? height : heightAdj }} className='w-100 d-md-flex justify-content-between scroll-y z-0'>
            <SelectedSKUList
                hideSKUList={hideSKUList}
                activeSku={activeSku}
                setActiveSKU={setActiveSKU}
                moduleName={moduleName}
                moduleEntry={repackEntryData}
                setUpdateEntry={setUpdateEntry}
            />
            <WeightEntryList
                setActiveSKU={setActiveSKU}
                setHideSKUList={setHideSKUList}
                hideSKUList={hideSKUList}
                moduleName={moduleName}
                moduleEntry={repackEntryData}
                activeSku={activeSku}
                setUpdateEntry={setUpdateEntry}
                updateEntry={updateEntry}
            />
        </div>
    )
}

export default WeightEntry