import React, { useState } from 'react'
import Calculator from 'Component/Calculator'
import { useDimension } from 'Page/StockEntry/GeneralServices'
const Decimal = ({
    isEmpty,
    entryListIdx,
    poListIdx,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    title,
    toCompare,
    useCalculator,
    singleView
}) => {
    const show = uom === 'gram' || uom === 'kg' || uom === 'KG' ? true : false
    const [showCalculator, setShowCalculator] = useState()
    const { isMobile } = useDimension()

    return (
        <div className={` ${inputWidth} mb-3 mr-3 ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>

            <div className={`${isEmpty ? 'border-red' : 'border'} rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input readOnly={useCalculator && isMobile ? true : false} onClick={() => setShowCalculator(!showCalculator)} id={`wgt_${poListIdx}_${entryListIdx}`} maxLength='12' autoComplete='off' type='text' className='w-100 plain-input' value={values} name={toCompare ? 'weightToCompare' : 'weight'} onChange={(e) => onChange(e)} />
                <div className='font-soft-grey'>{uom ? uom.toUpperCase() : ''}</div>
            </div>
            <Calculator
                singleView={singleView}
                useCalculator={useCalculator}
                className={showCalculator ? null : 'd-none'}
                setShowCalculator={setShowCalculator}
                showCalculator={showCalculator}
                onChange={(val) => onChange(null, val)}
                name={toCompare ? 'weightToCompare' : 'weight'}
                entryListIdx={entryListIdx}
                poListIdx={poListIdx} />
        </div>
    )
}

export default Decimal