import React, { useState } from 'react'
import Calculator from 'Component/Calculator'
const Pack = ({
    isEmpty,
    entryListIdx,
    poListIdx,
    uom,
    inputWidth,
    values,
    onChange,
    showTitle,
    placeholder,
    title,
    className,
    useCalculator,
    singleView
}) => {
    const show = uom === 'gram' || uom === 'kg' ? false : true
    const [showCalculator, setShowCalculator] = useState()

    return (
        <div className={` ${inputWidth} mb-3 mr-3 ${className} ${show ? null : 'd-none'}`}>
            <div className={` pb-1 LatoBold ${showTitle ? null : 'd-none'}`}>{title}</div>
            <div className={`${isEmpty ? 'border-red' : 'border'} rounded border-soft-green w-100 bg-white d-flex align-items-center input-height px-2 mr-3`}>
                <input onClick={() => setShowCalculator(!showCalculator)} id={`pack_${poListIdx}_${entryListIdx}`} maxLength='10' autoComplete='off' type='text' className='w-100 plain-input' value={values} name='pack' placeholder={placeholder} onChange={(e) => onChange(e)} />
                <div className='font-soft-grey'>{uom}</div>
            </div>
            <Calculator
                singleView={singleView}
                useCalculator={useCalculator}
                className={showCalculator ? null : 'd-none'}
                setShowCalculator={setShowCalculator}
                showCalculator={showCalculator}
                onChange={(val) => onChange(null, val)}
                name={'pack'}
                entryListIdx={entryListIdx}
                poListIdx={poListIdx} />
        </div>
    )
}

export default Pack